  .preloader__body {
    transform: translate3d(-50%, -100%, 0); }
  .preloader__letter {
    font-size: 4rem; }
  .preloader__logo-bolder {
    height: 1px; }
    .preloader__logo-bolder::before, .preloader__logo-bolder::after {
      height: 1px;
      left: -13px; } }

